@font-face{
    font-family: 'Comfortaa';
    src: url(../style/font/Comfortaa-VariableFont_wght.ttf);
}

@font-face{
    font-family: 'Comfortaa-bold';
    src: url(../style/font/Comfortaa-Bold.ttf);
}

@font-face{
    font-family: 'ubuntu-regular';
    src: url(../style/font/Ubuntu-Regular.ttf);
}

@font-face{
    font-family: 'vollkorn-regular';
    src: url(../style/font/Vollkorn-VariableFont_wght.ttf);
}


:root {
    --main-color: #068888   ;
    --abu1: #626262;
    --abu2: #ACACAC;
  }


.ubuntu-reg{
    font-family: 'ubuntu-regular';
}

.comfortaa{
    font-family: 'Comfortaa';
}

.comfortaa-bold{
    font-family: 'Comfortaa-bold';
}

.vollkorn-reg{
    font-family: 'vollkorn-regular';
}

.maincolor{
    color: var(--main-color)
}

.abu1{
    color: var(--abu1);
}

.abu2{
    color: var(--abu2);
}

.fullScr{
    height: 100vh;
    width: 100wh;
    background: #fff;
}

.kotak{
    border: 1px solid red
}

.topContent{
    height: 138px;
    width: 100%;
    padding: 32px;
}

.topLogo{
    height: 100%;
    height: 92px;
    width: 92px;
}

.topMasjid{
    font-size: 64px;
    font-weight: 700;
}

.topTanggal{
    height: 80px;
    width: 500px;
}

.date{
    font-size: 30px;
    font-weight: bold;
}

.hijri{
    border-right: 4px solid var(--main-color);
    text-align: right;
    
}

.maincontent{
    height:788px;
}
.mainImage{
    width: 1790px;
    height: 788px;
    overflow: hidden;
    border-radius: 20px;
    box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.25);
    position: fixed;
    background-image: url(../../asset/261000.jpg);
    background-size: cover;
    padding: 15px;
}

.waktu{
    margin-top: 226px;
    height: 496px;
}

.waktuSholat{
    width: 271px;
    height: 431px;
    background-color: #fff;
    /* opacity: 80%; */
    border-radius: 12px;
    box-shadow: 6px 6px 4px rgba(0, 0, 0, 0.3);
    transition: all 0.6s ease-in-out;
}

.selected{
    width: 312px;
    height: 496px;
    transition: all 0.6s ease-in-out;
}

.selected .jam{
    font-size: 116px;
    transition: all 0.6s ease-in-out;
}
.bgwaktu{
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.3);
}

.line100{
    line-height: 128px;
}

.bgwaktu img{
    width: 100%;
}

.sholat{
    font-weight: bold;
    font-size: 60px;
    text-align: right;
    transition: 0ms;
}

.jam{
    font-weight: bold;
    font-size: 96px;
    text-align: center;
    line-height: 128px;
    transition: all 0.6s ease-in-out;
}

.currentClock{
    position: absolute;
    right: 20px;
    top: 20px;
}

.clock{
    font-size: 96px;
    color: white;
    line-height: 107px;
}

.hari{
    font-size: 48px;
    color: white;
    text-align: right;
}

.alamat{
    width: 100%;
    font-size: 24px;
    text-align: center;
}

.timer{
    height: 35px;
    width: 100%;
    text-align: center;
    color: white;
    /* background-color: var(--main-color); */
    border-radius: 0 0 12px 12px;
    
}

.textBerjalan{
    background-color: #fff;
    opacity: 80%;
    border-radius: 20px;
    height: 40px;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color:black;
    padding: 0 24px;
}

